import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Reservation from '../components/templates/templates-reservation/Reservation';
import {
  clientMessageChange,
  detailToReserveLocation,
  detailToReserveRoom,
  detailToReserveDate,
  detailToReserveGuest,
  reserving,
  changeDate,
  changeGuest,
  clearSuccessMessage,
} from '../modules/reserve';
import { dateInput, specificInputClear } from '../modules/search';
import { clearCheckDateDtail, dateChangeDetail } from '../modules/detail';
import BootPay from 'bootpay-js';
import { useHistory } from 'react-router-dom';
import qs from 'query-string';

const ReservationContainer = () => {
  // store   state
  const dispatch = useDispatch();
  const history = useHistory();

  const { loading } = useSelector((state) => state);

  const paymentLoading = loading['reserve/RESERVING'];

  const token = useSelector((state) => state.auth.token);

  const { adults, children, infants, check_in, check_out } = qs.parse(
    history.location.search,
  );

  // detail redux reserve     
  const {
    id,
    name,
    roomCost,
    cleaningCost,
    tax,
    peopleLimit,
    description,
    bedNum,
    bathRoomNum,
    grade,
    hostName,
    hostImgURL,
    commentCount,
    roomImgUrlList,
  } = useSelector(({ detail }) => detail.infoRes);

  console.log(id, commentCount, '');

  const { startDate, endDate, numOfAdult, numOfKid, numOfInfant } = useSelector(
    ({ detail }) => detail,
  );

  // detail redux ,    
  const guestSearch = {
    numOfAdult: parseInt(numOfAdult),
    numOfKid: parseInt(numOfKid),
    numOfInfant: parseInt(numOfInfant),
  };

  // reserve redux   ,   
  const { message, checkDateSearch: checkDate } = useSelector(
    (state) => state.reserve,
  );

  const totalCost = 2000;

  const {
    numOfAdult: adult,
    numOfKid: kid,
    numOfInfant: infantNumber,
  } = useSelector(({ reserve }) => reserve.guestSearch);

  const { id: roomId, roomCost: reserveRoomCost, name: testName } = useSelector(
    ({ reserve }) => reserve.infoRes,
  );

  const { infoRes, reserveError } = useSelector(({ reserve }) => reserve);

  const { locationDetail } = useSelector(({ detail }) => detail.infoRes);

  const { locationDetail: reserveLocationDetail } = useSelector(
    (state) => state.reserve,
  );

  const { roomImgUrlList: RoomTablePhotoImgURL } = useSelector(
    ({ reserve }) => reserve,
  );

  const checkDateSearch = { startDate, endDate };

  // console.log(checkDateSearch);

  // search  
  const { startDate: checkIn, endDate: checkOut } = checkDate;

  //   +  
  const guestNumber = adult + kid;

  //    
  const [dateModal, setDateModal] = useState(false);
  const [guestModal, setGuestModal] = useState(false);
  const [comfirmModal, setComfirmModal] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  const manageDateModal = useCallback(() => {
    setDateModal(!dateModal);
  }, [dateModal]);

  const manageGuestModal = useCallback(() => {
    setGuestModal(!guestModal);
    console.log('heeee');
    dispatch(changeGuest('guestSearch', 'numOfAdult', +adults));
    dispatch(changeGuest('guestSearch', 'numOfKid', +children));
    dispatch(changeGuest('guestSearch', 'numOfInfant', +infants));
  }, [guestModal]);

  // textArea state  event function
  const change = useCallback(
    (e) => {
      const value = e.target.value;

      dispatch(clientMessageChange(value));
    },
    [dispatch],
  );

  //  button   event function
  const click = useCallback(() => {
    setComfirmModal(true);

    BootPay.request({
      price: 2000, //  
      application_id: '6024e5ee5b2948001d52037b',
      name: testName, //  
      pg: 'nicepay',
      method: 'card', //,      .
      show_agree_window: 0, //      
      items: [],
      user_info: {
        username: ' ',
        email: ' ',
        addr: ' ',
        phone: '010-1234-4567',
      },
      order_id: 'order_id_1234', // ,    .
      params: {},
      account_expire_at: '', //    ( yyyy-mm-dd  .  . )
      extra: {
        start_at: '2019-05-10', //    -         Billing key 
        end_at: '2022-05-10', //   -    - 
        vbank_result: 0, //   ,   (1), (0),  (1)
        quota: '0', //  5      , [0(), 2, 3] ,  12 ,
        theme: 'purple', // [ red, purple(), custom ]
        custom_background: '#00a086', // [ theme custom   background    ]
        custom_font_color: '#ffffff', // [ theme custom   font color    ]
      },
    })
      .error((data) => console.log(data))
      .confirm(({ price, receipt_id }) => {
        try {
          dispatch(
            reserving(
              roomId,
              checkIn,
              checkOut,
              guestNumber,
              infantNumber,
              totalCost,
              message,
              token,
              price,
              receipt_id,
            ),
          );

          // history.push('/reserve');
        } catch (err) {
          console.log(err);
          // history.push('/');
        }

        BootPay.removePaymentWindow();
      });
  }, [
    // reserveRoomCost,
    dispatch,
    testName,
    roomId,
    checkIn,
    checkOut,
    guestNumber,
    infantNumber,
    totalCost,
    message,
    token,
  ]);

  const saveDate = useCallback(() => {
    setDateModal(!dateModal);
    dispatch(dateInput('startDate', checkIn)); //    dispatch
    dispatch(dateInput('endDate', checkOut)); //    dispatch
    dispatch(dateChangeDetail('startDate', checkIn)); //    dispatch
    dispatch(dateChangeDetail('endDate', checkOut)); //    dispatch
    history.push({
      pathname: '/reserve',
      search:
        '?' +
        new URLSearchParams({
          roomId,
          check_in: checkIn,
          check_out: checkOut,
          adults: adult,
          children: kid,
          infants: infantNumber,
        }).toString(),
    });
  }, [
    dispatch,
    dateModal,
    checkIn,
    checkOut,
    numOfAdult,
    numOfKid,
    numOfInfant,
  ]);

  //     
  const { reserveSuccess } = useSelector((state) => state.reserve);

  const [completeModalState, setCompleteModalState] = useState(false);

  const moveToHomeClick = () => {
    setCompleteModalState(false);
    console.log('');
    dispatch(clearSuccessMessage());
    history.push('./');
  };

  useEffect(() => {
    if (!checkIn || !checkOut) {
      dispatch(changeDate('startDate', check_in));
      dispatch(changeDate('endDate', check_out));
    }
  }, [dateModal]);

  useEffect(() => {
    if (reserveSuccess) {
      setCompleteModalState(true);
      document.body.style.overflowY = 'hidden';
    }

    dispatch(
      detailToReserveRoom(
        id,
        name,
        roomCost,
        cleaningCost,
        tax,
        peopleLimit,
        description,
        bedNum,
        bathRoomNum,
        grade,
        hostName,
        hostImgURL,
        commentCount,
        roomImgUrlList,
      ),
    );
    dispatch(detailToReserveLocation(locationDetail));
    dispatch(detailToReserveDate(startDate, endDate));
    dispatch(detailToReserveGuest(numOfAdult, numOfKid, numOfInfant));

    dispatch(changeGuest('guestSearch', 'numOfAdult', +adults));
    dispatch(changeGuest('guestSearch', 'numOfKid', +children));
    dispatch(changeGuest('guestSearch', 'numOfInfant', +infants));
    dispatch(changeDate('startDate', check_in));
    dispatch(changeDate('endDate', check_out));

    setTimeout(() => {
      setIsLoading(false);
    }, 1000);

    return () => {
      document.body.style.overflowY = 'unset';
      // setCompleteModalState(false);
    };
  }, [
    dispatch,
    locationDetail,
    startDate,
    endDate,
    numOfAdult,
    numOfKid,
    numOfInfant,
    id,
    name,
    roomCost,
    cleaningCost,
    tax,
    peopleLimit,
    description,
    bedNum,
    bathRoomNum,
    grade,
    hostName,
    hostImgURL,
    commentCount,
    roomImgUrlList,
    setCompleteModalState,
    reserveSuccess,
    check_in,
    check_out,
    adults,
    children,
    infants,
  ]);

  return (
    <Reservation
      change={change}
      click={click}
      dateModal={dateModal}
      manageDateModal={manageDateModal}
      guestModal={guestModal}
      manageGuestModal={manageGuestModal}
      setGuestModal={setGuestModal}
      comfirmModal={comfirmModal}
      checkDateSearch={checkDateSearch}
      guestSearch={guestSearch}
      checkDate={checkDate}
      saveDate={saveDate}
      isLoading={isLoading}
      infoRes={infoRes}
      reserveLocationDetail={reserveLocationDetail}
      RoomTablePhotoImgURL={RoomTablePhotoImgURL}
      completeModalState={completeModalState}
      moveToHomeClick={moveToHomeClick}
      paymentLoading={paymentLoading}
    />
  );
};

export default ReservationContainer;
