export const userInfoNameRemake = ({ token, name }) => {
  const body = { name };

  const headers = { headers: { Authorization: `Bearer ${token}` } };
  console.log(body);
  return client.post('https://backend.kbnb.tk/user/update/name', body, headers);
};
