export const reserveCancel = ({ token, reservationId, name, reason }) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
    data: {
      reservationId,
      name,
      reason,
    },
  };

  return client.delete(`https://backend.kbnb.tk/reservation`, config);
};

//  
export const writeReview = ({
  token,
  reservationId,
  cleanliness,
  accuracy,
  communication,
  locationRate,
  checkIn,
  priceSatisfaction,
  description,
}) => {
  const headers = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const data = {
    reservationId,
    cleanliness,
    accuracy,
    communication,
    locationRate,
    checkIn,
    priceSatisfaction,
    description,
  };

  return client.post(`https://backend.kbnb.tk/comment`, data, headers);
};

// ,  
export const roomCheck = ({ token, roomId }) => {
  const headers = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };
  const data = {
    roomId,
  };

  return client.patch(`https://backend.kbnb.tk/room/check`, data, headers);
};
//    

export const getReservedRoomInfo = ({ token, reservationId }) => {
  const headers = { headers: { Authorization: `Bearer ${token}` } };

  return client.get(
    `https://backend.kbnb.tk/reservation/detail?reservationId=${reservationId}`,
    headers,
  );
};
