import React from 'react';
import styled from 'styled-components';
import moment from 'moment';
import ReservationContent from '../../atoms/atoms-reservation/ReservationContent';
import ReservationTitle from '../../atoms/atoms-reservation/ReservationTitle';
import ReservationUnderLine from '../../atoms/atoms-reservation/ResevationUnderLine';
import TextStyle from '../../atoms/atoms-reservation/TextStyle';

const Container = styled.section`
  width: 100%;
  padding-left: 2.4rem;
  border-left: 0.8rem solid rgb(255, 175, 15);
`;

const PaddingContainer = styled(ReservationContent)`
  padding: 2.4rem 0;
`;

const ReservationRefundOrganism = ({ refundDate }) => {
  const x = moment(refundDate).subtract(1, 'days').format('MM-DD');

  const arr = x.split('-');

  return (
    <>
      <Container>
        <ReservationTitle sub children=" " />
        <PaddingContainer
          normal
          children={`${arr[0]} ${arr[1]} 3:00 PM     1       50% .`}
        />
        <div>
          <TextStyle
            blackSmallBold
            children="      ."
          />
          <TextStyle
            graySmallNormal
            children="19                        "
          />
        </div>
      </Container>
      <ReservationUnderLine />
    </>
  );
};

export default ReservationRefundOrganism;
