import React, { useRef } from 'react';
import './LocationSearchInput.css';
import { useLoadScript } from '@react-google-maps/api';
import '@reach/combobox/styles.css';
import { FaMapMarkerAlt } from 'react-icons/fa';
import {
  Combobox,
  ComboboxInput,
  ComboboxPopover,
  ComboboxList,
  ComboboxOption,
} from '@reach/combobox';
import usePlacesAutocomplete, {
  getGeocode,
  getLatLng,
} from 'use-places-autocomplete';
import { useDispatch, useSelector } from 'react-redux';
import {
  locationInput,
  destinationInput,
  getLengthForZoom,
} from '../../../../modules/search';

const libraries = ['places'];

// app  
const LocationSearchInput = ({
  SearchTypeHandler,
  moveFocusNext,
  setNavModalState,
}) => {
  const { isLoaded, loadError } = useLoadScript({
    googleMapsApiKey: 'AIzaSyDi2VswS8ZRJ3Vk6aDl0Mx3RbxI27GeXbQ',
    libraries,
  });

  if (loadError) return '';
  if (!isLoaded) return '';

  return (
    <div className="location-search-input-outer-container">
      <Search
        SearchTypeHandler={SearchTypeHandler}
        setNavModalState={setNavModalState}
        moveFocusNext={moveFocusNext}
      />
    </div>
  );
};

function Search({ panTo, SearchTypeHandler, moveFocusNext, setNavModalState }) {
  const {
    ready,
    value, // value  input  
    suggestions: { status, data }, // suggestions  api  , status data .
    setValue,
    clearSuggestions,
  } = usePlacesAutocomplete({
    requestOptions: {
      location: {
        lat: () => 43.653225,
        lng: () => -79.383186,
      }, // user   
      radius: 200 * 1000, // 1000     km   . 200km ,
    },
  }); // {}    

  const dispatch = useDispatch();
  const destinationName = useSelector(({ search }) => search.destinationName);
  const { latitude } = useSelector(
    ({ search }) => search.searchReq.locationSearch,
  );
  return (
    <>
      <Combobox
        className="combo-box"
        onSelect={async (address) => {
          setValue(address, false); //   address    false 
          clearSuggestions(); //    .  .    .

          // address    
          try {
            const results = await getGeocode({ address }); //   address   promise .
            const { lat, lng } = await getLatLng(results[0]); //  lat lng 
            console.log(results[0]);
            dispatch(
              //  store 
              locationInput({
                latitude: lat,
                longitude: lng,
                latitudeMax: results[0].geometry.viewport.Ua.i,
                latitudeMin: results[0].geometry.viewport.Ua.g,
                longitudeMax: results[0].geometry.viewport.La.i,
                longitudeMin: results[0].geometry.viewport.La.g,
              }),
            );

            dispatch(getLengthForZoom(results[0].address_components.length));
            moveFocusNext('location');
            document.querySelector('.combo-box-input').blur();
          } catch (error) {
            console.error(error);
          }
        }}
      >
        <ComboboxInput
          select
          change
          value={destinationName}
          onChange={(e) => {
            setValue(e.target.value);
            SearchTypeHandler('location');
            dispatch(
              locationInput({
                latitude: '',
                longitude: '',
                latitudeMax: '',
                latitudeMin: '',
                longitudeMax: '',
                longitudeMin: '',
              }),
            );
            dispatch(destinationInput(e.target.value)); // DestinationName 
          }}
          disabled={!ready} //    .
          placeholder=" ?"
          className="combo-box-input"
          id="locationInput"
          onClick={(e) => {
            SearchTypeHandler('location');
          }}
          onBlur={() => {}}
          autoComplete="off"
          selectOnClick={true}
        />
        {!latitude && ( // locationSearch        .
          <ComboboxPopover className="combo-box-pop-over">
            <ComboboxList className="combo-box-list">
              {status === 'OK' &&
                data.map(({ id, description }) => (
                  <div className="combo-box-options-container">
                    <div className="combo-box-marker-container">
                      <FaMapMarkerAlt className="gray-marker" />
                    </div>
                    <ComboboxOption
                      className="combo-box-options"
                      key={id}
                      value={description}
                      onKeyDown={(e) => {
                        if (e.key !== 'Enter') {
                          e.preventDefault();
                          return;
                        }
                      }}
                    />
                  </div>
                ))}
            </ComboboxList>
          </ComboboxPopover>
        )}
      </Combobox>
    </>
  );
}

export default LocationSearchInput;
