import moment from 'moment';

//   
export const checkNameValidation = (name) => {
  const numberOrScRegExp = /^[-a-zA-Z]{2,20}$/;
  return numberOrScRegExp.test(name);
};

// Date  .  true   
export const checkDateValidation = (date) => {
  const dateRegExp = /^(19|20)\d{2}-(0[1-9]|1[012])-(0[1-9]|[12][0-9]|3[0-1])$/; // date 
  if (!dateRegExp.test(date)) return false;

  return moment().diff(moment(date), 'years') >= 18; //  18  true
};

//   
export const checkEmailValidation = (email) => {
  const emailRegExp = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i; //  
  return emailRegExp.test(email);
};

//  
export const checkPasswordValidation = (
  nameValue = '',
  emailValue = '',
  password = '',
) => {
  const passwordRegExp = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,}$/; // , ,   

  const isLongerThanEight = password.length >= 8 ? true : false; // 8  
  const hasEveryCharacter = passwordRegExp.test(password); // , ,     
  const prevEmail = emailValue.split('@')[0];
  const doesContainInfo =
    !!password &&
    !password.match(nameValue) &&
    !password.match(prevEmail) &&
    !password.match(nameValue) &&
    !password.match(prevEmail); //     false,

  return {
    isLongerThanEight,
    hasEveryCharacter,
    doesContainInfo,
  };
};
