import React from 'react';
import styled from 'styled-components';
import { moneyfilter } from '../../../../lib/moneyfilter';
import PriceListItem from './PriceList';

const PriceBox = styled.ul`
  display: block;
  margin-top: 24px;
  margin-block-end: 0;
  padding: 0;
  color: rgb(34, 34, 34);

  .flexbox {
    display: flex;
    justify-content: space-evenly;
  }
`;

const PriceDetail = ({ infoRes, totalSchedule }) => {
  const roomPrice = `${moneyfilter(infoRes.roomCost)} X ${totalSchedule}`;

  return (
    <PriceBox>
      <PriceListItem
        icon=""
        detail={roomPrice}
        price={moneyfilter(infoRes.roomCost * totalSchedule)}
      />
      <PriceListItem detail="" price={moneyfilter(infoRes.cleaningCost)} />
      <PriceListItem
        detail=" "
        price={moneyfilter((infoRes.roomCost * totalSchedule) / 10)}
      />
      <PriceListItem detail=" " price={moneyfilter(infoRes.tax)} />
    </PriceBox>
  );
};

export default PriceDetail;
