const PersonalInfoUIStyle = styled.ul`
  display: flex;
  flex-grow: 2;
  /* max-width: 500px; */
  /* min-width: 500px; */
  flex-direction: column;
  padding: 0;
  margin: 10px 20px 0 20px;
  li {
    div {
      /* border: 1px solid; */
    }
    .imgPerson {
      width: 320px;
      /* height: 20%; */

      img {
        width: 100%;
      }
    }
    .btn {
      position: absolute;
      top: 0;
      right: 0;
      &:focus {
        outline: none;
        border-bottom: 1px solid rgba(0, 0, 0, 0.3);
      }
      &:disabled {
        background: none;
        color: rgba(0, 0, 0, 0.3);
        cursor: not-allowed;
      }
      &:disabled:hover {
        text-decoration: none;
      }
    }
  }
`;

const PersonalInfoUl = ({
  fix,
  fixInfoBtn,
  cancelclick,
  personInfoChange,
  name,
  email,
  birth,
  imageUrl,
  inputFocus,
  ChangeInputBtn,
  userInfo,
  setFix,
  loading,
  emailCheck,
  personInfoEmailSubmit,
  emailOk,
  personInfoEmailSubmitKeypress,
  cancelModalEmail,
  nameRef,
  KeyDown,
}) => {
  // const [emailOk, setEmailOk] = useState(false);

  const [imageImg, setImageImg] = useState();

  useEffect(() => {
    setImageImg(imageUrl);
  }, [imageImg, imageUrl]);
  console.log(imageImg);

  return (
    <PersonalInfoUIStyle onClick={fix.cancel ? cancelclick : fixInfoBtn}>
      <PersonalInfoLi>
        <div>
          <TextStyle></TextStyle>
          {fix.name ? (
            <PersonalNameInput
              name={name}
              fix={fix}
              personInfoChange={personInfoChange}
              inputFocus={inputFocus}
              ChangeInputBtn={ChangeInputBtn}
              loading={loading}
              nameRef={nameRef}
              KeyDown={KeyDown}
            />
          ) : (
            <TextStyle>{name}</TextStyle>
          )}
        </div>
        <Button
          name="name"
          className="btn"
          greenText
          tabIndex="0"
          disabled={!!fix.img || !!fix.birth || !!fix.emailAddress}
        >
          {!fix.name ? '' : ''}
        </Button>
      </PersonalInfoLi>
      <PersonalInfoLi>
        <div className="gender">
          <TextStyle></TextStyle>
          {fix.img ? (
            <PersonalInfoImg
              name={name}
              email={email}
              birth={birth}
              setFix={setFix}
              personInfoChange={personInfoChange}
              imageUrl={imageUrl}
              loading={loading}
              fix={fix}
            />
          ) : (
            <>
              {imageUrl ? (
                <div className="imgPerson">
                  <img src={imageImg} alt="hello" />
                </div>
              ) : (
                <TextStyle> </TextStyle>
              )}
            </>
          )}
        </div>
        <Button
          name="imageUrl"
          className="btn"
          disabled={!!fix.name || !!fix.birth || !!fix.emailAddress}
          greenText
        >
          {!fix.img ? '' : ''}
        </Button>
      </PersonalInfoLi>
      <PersonalInfoLi>
        <div>
          <TextStyle></TextStyle>
          <TextStyle>
            {birth
              ? extractMonthDate(userInfo.birth)?.year &&
                extractMonthDate(userInfo.birth)?.month &&
                extractMonthDate(userInfo.birth)?.date
                ? `${
                    extractMonthDate(userInfo.birth)?.year &&
                    extractMonthDate(userInfo.birth)?.year
                  } ${
                    extractMonthDate(userInfo.birth)?.month &&
                    extractMonthDate(userInfo.birth)?.month
                  } ${
                    extractMonthDate(userInfo.birth)?.date &&
                    extractMonthDate(userInfo.birth)?.date
                  }`
                : ' '
              : ' '}
          </TextStyle>
          {fix.birth && (
            <PersonalInfoBirthinput
              ChangeInputBtn={ChangeInputBtn}
              personInfoChange={personInfoChange}
              birth={birth}
              loading={loading}
              fix={fix}
            />
          )}
        </div>
        <Button
          name="birth"
          className="btn"
          disabled={!!fix.img || !!fix.name || !!fix.emailAddress}
          greenText
        >
          {!fix.birth ? '' : ''}
        </Button>
      </PersonalInfoLi>
      <PersonalInfoLi>
        <div>
          <TextStyle> </TextStyle>
          {fix.emailAddress ? (
            <PersonalInfoEmailInput
              name={name}
              email={email}
              birth={birth}
              imageUrl={imageUrl}
              personInfoChange={personInfoChange}
              ChangeInputBtn={ChangeInputBtn}
              loading={loading}
              emailCheck={emailCheck}
              fix={fix}
            />
          ) : (
            <TextStyle>{email}</TextStyle>
          )}
          {(emailOk || loading['user/CHANGE_INPUT_USER_EMAIL_SUBMIT']) && (
            <Modal>
              <NoEmailModal
                emailCheck={emailCheck}
                loading={loading}
                email={email}
                ChangeInputBtn={ChangeInputBtn}
                personInfoChange={personInfoChange}
                personInfoEmailSubmit={personInfoEmailSubmit}
                emailOk={emailOk}
                personInfoEmailSubmitKeypress={personInfoEmailSubmitKeypress}
                cancelModalEmail={cancelModalEmail}
              ></NoEmailModal>
            </Modal>
          )}
        </div>
        <Button
          name="emailAddress"
          disabled={!!fix.img || !!fix.birth || !!fix.name}
          className="btn"
          greenText
        >
          {!fix.emailAddress ? '' : ''}
        </Button>
      </PersonalInfoLi>
      <PersonalInfoLi>
        <div>
          <TextStyle></TextStyle>
          <TextStyle> </TextStyle>
        </div>
        <Button
          className="btn"
          disabled={
            !!fix.img || !!fix.birth || !!fix.name || !!fix.emailAddress
          }
          greenText
        >
          
        </Button>
      </PersonalInfoLi>
      <PersonalInfoLi>
        <div>
          <TextStyle>  </TextStyle>
          <TextStyle> </TextStyle>
        </div>
        <Button
          className="btn"
          disabled={
            !!fix.img || !!fix.birth || !!fix.name || !!fix.emailAddress
          }
          greenText
        >
          
        </Button>
      </PersonalInfoLi>
      <PersonalInfoLi>
        <div>
          <TextStyle></TextStyle>
          <TextStyle> </TextStyle>
        </div>
        <Button
          disabled={
            !!fix.img || !!fix.birth || !!fix.name || !!fix.emailAddress
          }
          className="btn"
          greenText
        >
          
        </Button>
      </PersonalInfoLi>
      <PersonalInfoLi>
        <div>
          <TextStyle> </TextStyle>
          <TextStyle> </TextStyle>
        </div>
        <Button
          disabled={
            !!fix.img || !!fix.birth || !!fix.name || !!fix.emailAddress
          }
          className="btn"
          greenText
        >
          
        </Button>
      </PersonalInfoLi>
      <PersonalInfoLi>
        <div>
          <TextStyle>    </TextStyle>
          <TextStyle> </TextStyle>
        </div>
        <Button
          disabled={
            !!fix.img || !!fix.birth || !!fix.name || !!fix.emailAddress
          }
          className="btn"
          greenText
        >
          
        </Button>
      </PersonalInfoLi>
    </PersonalInfoUIStyle>
  );
};

export default PersonalInfoUl;
