import React, { useEffect, useState } from 'react';
import { useHistory, useRouteMatch } from 'react-router-dom';
import HeaderMain from '../../components/templates/templates-header/HeaderMain';
import HeaderList from '../../components/templates/templates-header/HeaderList';
import HeaderDetail from '../../components/templates/templates-header/HeaderDetail';
import { useDispatch, useSelector } from 'react-redux';
import SkipNavigation from '../../components/templates/templates-header/SkipNabigation';
import { destinationInput, searching } from '../../modules/search';

const HeaderContainer = ({
  DetailHeaderRef,
  ImageContainerRef,
  reviewRef,
  facilityRef,
  modal,
  setModal,
  isOpen,
  setIsOpen,
  formState,
  setFormState,
  bookingInfoRef,
  isCalendarOpen,
  setIsCalendarOpen,
  isGuestOpen,
  setIsGuestOpen,
}) => {
  const history = useHistory();
  const match = useRouteMatch();

  //     
  const {
    locationSearch,
    checkDateSearch,
    guestSearch,
    costSearch,
    roomType,
    bedNum,
    bedRoomNum,
    bathRoomNum,
  } = useSelector((state) => state.search.searchReq);

  const [isScrolled, setIsScrolled] = useState(false); // scrollY 80 UI 
  const [isClicked, setIsClicked] = useState(false); // HeaderMainSearchNav    UI  .
  const initialNavModalState = {
    location: false,
    checkIn: false,
    checkOut: false,
    guest: false,
  };
  const [navModalState, setNavModalState] = useState(initialNavModalState); //   4 open 
  const [isClickedOutside, setIsClickedOutside] = useState(false); //      

  const SearchTypeHandler = (searchType) => {
    setNavModalState({ ...initialNavModalState, [searchType]: true }); //   true .
  };

  //        , selectedType =      .
  const moveFocusNext = (selectedType) => {
    if (selectedType === 'location') SearchTypeHandler('checkIn');
    //  ->   
    else if (selectedType === 'checkIn') SearchTypeHandler('checkOut');
    //    ->    
    else if (selectedType === 'checkOut' && !checkDateSearch.checkIn)
      SearchTypeHandler('checkIn');
    //    &     ->     
    return;
  };

  //  nav bar    .
  const clickHandler = () => {
    // click nav bar     .
    setIsClicked(true);
  };

  useEffect(() => {
    const scrollHandler = () => {
      setIsClicked(false);

      if (window.scrollY >= 80) {
        setIsScrolled(true);
        return;
      }
      setIsScrolled(false);
    };

    window.addEventListener('scroll', scrollHandler);
    return () => {
      window.removeEventListener('scroll', scrollHandler);
    };
  }, []);

  useEffect(() => {
    if (isScrolled && isClicked) {
      setNavModalState({ ...initialNavModalState, location: true });
      return;
    }
  }, [isScrolled, isClicked]);

  useEffect(() => {
    dispatch(destinationInput(''));
  }, []);

  const dispatch = useDispatch();
  const serchBtn = () => {
    const id = 0;
    dispatch(
      searching({
        id,
        locationSearch,
        checkDateSearch,
        guestSearch,
        costSearch,
        roomType,
        bedNum,
        bedRoomNum,
        bathRoomNum,
      }),
    );
  };
  return (
    <>
      <SkipNavigation />
      {history.location.pathname === '/' && (
        <HeaderMain
          isScrolled={isScrolled}
          setIsScrolled={setIsScrolled}
          isClicked={isClicked}
          setIsClicked={setIsClicked}
          navModalState={navModalState}
          setNavModalState={setNavModalState}
          initialNavModalState={initialNavModalState}
          isClickedOutside={isClickedOutside}
          setIsClickedOutside={setIsClickedOutside}
          SearchTypeHandler={SearchTypeHandler}
          locationSearch={locationSearch}
          checkDateSearch={checkDateSearch}
          guestSearch={guestSearch}
          moveFocusNext={moveFocusNext}
          clickHandler={clickHandler}
          formState={formState}
          setFormState={setFormState}
          serchBtn={serchBtn}
        />
      )}

      {history.location.pathname === '/rooms' && (
        <HeaderList
          isScrolled={isScrolled}
          setIsScrolled={setIsScrolled}
          isClicked={isClicked}
          setIsClicked={setIsClicked}
          navModalState={navModalState}
          setNavModalState={setNavModalState}
          initialNavModalState={initialNavModalState}
          SearchTypeHandler={SearchTypeHandler}
          locationSearch={locationSearch}
          checkDateSearch={checkDateSearch}
          guestSearch={guestSearch}
          moveFocusNext={moveFocusNext}
          clickHandler={clickHandler}
          formState={formState}
          setFormState={setFormState}
          modal={modal}
          setModal={setModal}
        />
      )}
      {match.params.roomId && (
        <HeaderDetail
          isScrolled={isScrolled}
          setIsScrolled={setIsScrolled}
          isClicked={isClicked}
          setIsClicked={setIsClicked}
          navModalState={navModalState}
          setNavModalState={setNavModalState}
          initialNavModalState={initialNavModalState}
          SearchTypeHandler={SearchTypeHandler}
          locationSearch={locationSearch}
          checkDateSearch={checkDateSearch}
          guestSearch={guestSearch}
          moveFocusNext={moveFocusNext}
          clickHandler={clickHandler}
          DetailHeaderRef={DetailHeaderRef}
          ImageContainerRef={ImageContainerRef}
          reviewRef={reviewRef}
          facilityRef={facilityRef}
          modal={modal}
          setModal={setModal}
          isOpen={isOpen}
          setIsOpen={setIsOpen}
          formState={formState}
          setFormState={setFormState}
          bookingInfoRef={bookingInfoRef}
          isCalendarOpen={isCalendarOpen}
          setIsCalendarOpen={setIsCalendarOpen}
          isGuestOpen={isGuestOpen}
          setIsGuestOpen={setIsGuestOpen}
        />
      )}
    </>
  );
};

export default HeaderContainer;
